import { useEffect, useState } from "react";
import { BASENAME, URL_PRODUCT_DETAIL } from "system/URL";
import { useInView } from "react-intersection-observer";
import { useHistory } from "react-router-dom";

const ProductList = ({ products = [], title = "" }) => {
  const [preId, setPreId] = useState(20);
  const [viewProducts, setViewProducts] = useState([
    {
      id: 1,
      title: "In Harm's Way",
      date: "2023-09-29",
      img: "data:image/png;base64,",
    },
    {
      id: 2,
      title: "Young Lions, The",
      date: "2024-01-06",
      img: "data:image/png;base64,",
    },
    {
      id: 3,
      title: "Third Person",
      date: "2023-03-21",
      img: "data:image/png;base64,",
    },
    {
      id: 4,
      title: "Avengers, The",
      date: "2023-03-08",
      img: "data:image/png;base64,",
    },
    {
      id: 5,
      title: "Messenger: The Story of Joan of Arc, The",
      date: "2023-11-07",
      img: "data:image/png;base64,",
    },
    {
      id: 6,
      title: "Best Boy",
      date: "2023-09-08",
      img: "data:image/png;base64,",
    },
    {
      id: 7,
      title: "Naked Harbour (Vuosaari)",
      date: "2023-10-13",
      img: "data:image/png;base64,",
    },
    {
      id: 8,
      title: "River Wild, The",
      date: "2023-08-25",
      img: "data:image/png;base64,",
    },
    {
      id: 9,
      title: "Suicide Club (Jisatsu saakuru)",
      date: "2023-10-15",
      img: "data:image/png;base64,",
    },
    {
      id: 10,
      title: "Flannel Pajamas",
      date: "2023-10-12",
      img: "data:image/png;base64,",
    },
    {
      id: 11,
      title: "The Horseplayer",
      date: "2023-09-06",
      img: "data:image/png;base64,",
    },
    {
      id: 12,
      title: "Sound and Fury",
      date: "2023-07-26",
      img: "data:image/png;base64,",
    },
    {
      id: 13,
      title: "White Chicks",
      date: "2023-03-28",
      img: "data:image/png;base64,",
    },
    {
      id: 14,
      title: "Where the Money Is",
      date: "2023-07-04",
      img: "data:image/png;base64,",
    },
    {
      id: 15,
      title: "China Blue",
      date: "2023-08-03",
      img: "data:image/png;base64,",
    },
    {
      id: 16,
      title: "Final Season, The",
      date: "2023-03-30",
      img: "data:image/png;base64,",
    },
    {
      id: 17,
      title: "At Five in the Afternoon (Panj é asr)",
      date: "2023-10-22",
      img: "data:image/png;base64,",
    },
    {
      id: 18,
      title: "Boys, The",
      date: "2023-10-23",
      img: "data:image/png;base64,",
    },
    {
      id: 19,
      title:
        "Sergeant Körmy and the Underwater Vehicles (Vääpeli Körmy ja vetenalaiset vehkeet)",
      date: "2023-05-08",
      img: "data:image/png;base64,",
    },
    {
      id: 20,
      title: "Bitch Slap",
      date: "2023-07-18",
      img: "data:image/png;base64,",
    },
  ]);

  const [ref, inView] = useInView();
  const history = useHistory();

  useEffect(() => {
    if (inView) {
      loadMore();
    }
  }, [inView]);

  const loadMore = () => {
    setViewProducts(
      viewProducts.concat(
        products.filter((item) => {
          return item.id > preId && item.id <= preId + 20;
        })
      )
    );

    setPreId(preId + 20);
  };

  return (
    <div id="main">
      <div id="contentContainer" className="is-open">
        {!!title && <div className="mini-title">{title}</div>}
        <div className={`container ${title ? "isTitle" : ""}`}>
          {viewProducts.map((item, idx) => {
            return (
              <div
                className="item"
                key={idx}
                ref={idx === viewProducts.length - 1 ? ref : null}
                onClick={() => {
                  history.push(`${URL_PRODUCT_DETAIL}/${item.id}`);
                }}
              >
                <img
                  src={`${BASENAME}/assets/img/sample500.png`}
                  alt="아이템"
                />
                <div className="title">{item.title}</div>
                <div className="date">{item.date}</div>
              </div>
            );
          })}
        </div>
      </div>
    </div>
  );
};

export default ProductList;
